@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');

* {
    margin: 0;
    padding: 0;
    width: auto;
    height: auto;
}

@media (min-width:767px) { 
    .tab-widget::-webkit-scrollbar {
        width: 5px;
    }
}

.close a {
    color: #fff;
}  

.Close {
    color: #fff;
    font-size: 35px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    float: right;
    cursor: pointer;
    margin: 10px 10px;
    padding: 2px 10px;
}

.title-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: auto;
}

.softwares-title {
    font-family: 'Kanit', sans-serif;
    font-size: 50px;
    color:#fff;
}

.search-div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.search-software {
    width: 180px;
    background: #fff;
    border-radius: 30px;
    color: #000;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-left: 10px;
    padding-right: 20px;
    margin-right: 20px;
}


.chatBtn {
    padding: 15px;
    border-radius: 25px;
    font-family: 'Poppins';
    text-transform: uppercase;
    background: #fff;
    color: #000;
}

.chatBtn:hover {
    background: inherit;
    border: 1px solid #000;
    text-decoration: none;
}

.search-div i {
    color: #000;
}

/* Entire Divison */
.softwares {
    background-size: cover;
    min-height: 100vh;
    overflow-x: hidden;
}

.softwares-div {
    margin-top: 20px;
}

/* Individual Software  */
.software hr {
    display: none;
}

.software {
    margin: 15px 0px;
    padding: 15px;
}

.software a {
    margin: 10px;
    float: right;
}

.software h4 {
    text-decoration: underline;
    font-family: 'Poppins';
    color: #C61752;
}

.software p {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

/* Tab Headers */
.tab-widget {
    background: #E8E8E8; 
    opacity: 0.7;
    border-radius: 10px;
}

@media (min-width:768px) {
    .tab-widget {
        height: 400px;
        overflow-x: hidden;
        overflow-y: auto;
        margin-left: 50px;
        margin-top: 10px;
    }    
}

.tab-widget button {
    padding: 10px;
    border: none;
    background: inherit;
    width: 100%;
    font-weight: 600;
    -ms-flex-align: center;
        align-items: center;
    font-family: 'Nunito', sans-serif;
}

.tab-widget .active {
    background: #fff;
    color: #000;
}
    

@media (min-width: 1200px) {
    .search-software {
        width: 230px;
    }
    .softwares-title {
        margin-left: 15px;
    }
    .chatBtn {
        margin-left: 12px;
    }
}

@media (max-width:767px) {
    .tab-widget {
        overflow-x: auto;
        white-space: nowrap;
        margin-top: 10px;
        margin-bottom: 20px;
    }   
    .tab-widget button {
        display: inline-block;
        overflow: hidden;
        font-size: medium;
        width: 80%;
    }
}

@media (max-width:500px) {
    .chatBtn span {
        display: none;
    }
    .chatBtn {
        font-size: 20px;
        padding: 10px;
        border-radius: 10px;
    }
    .search-software {
        width: 230px;
    }
}







