.app-div {
    background: rgb(255, 255, 255);
    width: 100px;
    height: 200px;
    max-width: 300px;
    display: inline-block;
    overflow-x: hidden;
    -webkit-transition: width .5s;
    -o-transition: width .5s;
    transition: width .5s;
    margin: 10px;
    border-radius: 10px;
    padding: 5px;
}

.app-div:hover {
    width: 300px;
}

.block img {
    height: 90px;
    width: 90px;
    text-align: center;
    border-radius: 10px;
}

.block h6,
.none h6 {
    font-weight: 700;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
}

.app-div-child {
    display: -ms-flexbox;
    display: flex;
}

.block {
    width: 100px;
}

.none {
    width: 200px;
    padding-left: 10px;
}

.block p {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.block .device {
    color: green;
}

.block .download-count {
    color: #121fdb;
}

.none .btn {
    background-color: transparent;
    color: white;
    border-radius: 25px;
}

.none p {
    height: 100px;
    width: 180px;
    white-space: initial;
    font-size: 14px;
}

.none .anchors a {
    font-weight: 800;
    font-family: 'Varela Round', sans-serif;
}



