.feeds {
    background-color: #fff;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    cursor: pointer;
  }
  .feeds .feed-title{
    font-family: 'Chau Philomene One', sans-serif;
  }

  .feeds .image img {
    margin: 10px;
  }

  .post-img {
    width: 100px;
    height: 70px;
  }
  
  .time-stamp {
    color: #053fddb4;
    background: #8bebfc38;
    text-align: center;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    font-family: cursive;
  }
  
  .Author{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 700;
    color:rgb(3, 3, 80);
  }
  .Author span{
    color:orange;
    font-style: italic;
  }

  @media screen and (max-width:600px) {
    .Author{
        display:none;
    }
    .post-img {
      width: 70px;
      height: 50px;
    }
  }


