@import url('https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

* {
    margin: 0;
    padding: 0;
    width: auto;
    height: auto;
}


.hacktools {
    background-size: cover;
    min-height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;
    margin-bottom: 20px;
}

.close {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    float: right;
    margin: 10px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.close i {
    color: red;
}

.title-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.hacktool-title {
    font-family: 'Bungee Inline', cursive;
    font-size: 60px;
}

.search {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.search i {
    color: blue;
}

.search-hack {
    min-width: 210px;
    color: #fff;
    border-radius: 25px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-left: 10px;
    padding-right: 20px;
    margin-right: 20px;
}


.card-widgit {
    width: 100%;
    padding: 5px;
    border-radius: 20px;
    margin-bottom: 20px;
}

.card-widgit h3 {
    color: #fff;
    padding-left: 10px;
    font-family: 'Fredoka One', cursive;
}

.scrollmenu {
    overflow: auto;
    white-space: nowrap;
}

@media (max-width:600px) {
    .hacktool-title {
        font-size: 40px;
    }
}

/* styling for different categories */
#Daily-drivers {
    background: rgb(1, 1, 65);
    background: -webkit-gradient(linear, left top, right top, from(rgba(1, 1, 65, 0.836)), to(rgba(0, 238, 255, 0.815)));
    background: -webkit-linear-gradient(left, rgba(1, 1, 65, 0.836) 0%, rgba(0, 238, 255, 0.815) 100%);
    background: -o-linear-gradient(left, rgba(1, 1, 65, 0.836) 0%, rgba(0, 238, 255, 0.815) 100%);
    background: linear-gradient(90deg, rgba(1, 1, 65, 0.836) 0%, rgba(0, 238, 255, 0.815) 100%);
}

#Entertainment {
    background: rgb(101, 17, 227);
    background: -webkit-gradient(linear, left top, right top, from(rgba(101, 17, 227, 0.863)), to(rgba(233, 6, 116, 0.76)));
    background: -webkit-linear-gradient(left, rgba(101, 17, 227, 0.863) 0%, rgba(233, 6, 116, 0.76) 100%);
    background: -o-linear-gradient(left, rgba(101, 17, 227, 0.863) 0%, rgba(233, 6, 116, 0.76) 100%);
    background: linear-gradient(90deg, rgba(101, 17, 227, 0.863) 0%, rgba(233, 6, 116, 0.76) 100%); 
}

#music-media {
    background: rgb(58, 18, 238);
    background: -webkit-gradient(linear, left top, right top, from(rgba(58, 18, 238, 0.795)), to(rgba(5, 170, 49, 0.856)));
    background: -webkit-linear-gradient(left, rgba(58, 18, 238, 0.795) 0%, rgba(5, 170, 49, 0.856) 100%);
    background: -o-linear-gradient(left, rgba(58, 18, 238, 0.795) 0%, rgba(5, 170, 49, 0.856) 100%);
    background: linear-gradient(90deg, rgba(58, 18, 238, 0.795) 0%, rgba(5, 170, 49, 0.856) 100%); 
}

#Editing {
    background: rgb(240, 12, 20);
    background: -webkit-radial-gradient(circle, rgba(240, 12, 20, 0.5074404761904762) 0%, rgba(240, 12, 246, 1) 100%);
    background: -o-radial-gradient(circle, rgba(240, 12, 20, 0.5074404761904762) 0%, rgba(240, 12, 246, 1) 100%);
    background: radial-gradient(circle, rgba(240, 12, 20, 0.5074404761904762) 0%, rgba(240, 12, 246, 1) 100%);
}

#Gaming {
    background: rgb(175, 10, 193);
    background: -webkit-gradient(linear, left top, right top, from(rgba(175, 10, 193, 0.815)), to(rgba(0, 251, 255, 0.781)));
    background: -webkit-linear-gradient(left, rgba(175, 10, 193, 0.815) 0%, rgba(0, 251, 255, 0.781) 100%);
    background: -o-linear-gradient(left, rgba(175, 10, 193, 0.815) 0%, rgba(0, 251, 255, 0.781) 100%);
    background: linear-gradient(90deg, rgba(175, 10, 193, 0.815) 0%, rgba(0, 251, 255, 0.781) 100%);
}

#Others {
    background: rgb(120, 60, 8);
    background: -webkit-gradient(linear, left top, right top, from(rgba(120, 60, 8, 0.801)), to(rgba(199, 199, 199, 0.76)));
    background: -webkit-linear-gradient(left, rgba(120, 60, 8, 0.801) 0%, rgba(199, 199, 199, 0.76) 100%);
    background: -o-linear-gradient(left, rgba(120, 60, 8, 0.801) 0%, rgba(199, 199, 199, 0.76) 100%);
    background: linear-gradient(90deg, rgba(120, 60, 8, 0.801) 0%, rgba(199, 199, 199, 0.76) 100%);
}




