@import url('https://fonts.googleapis.com/css2?family=Coda+Caption:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Chau+Philomene+One&display=swap');
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}

.technews {
  background-size: cover;
  min-height: 100vh;
  min-width: 100%;
  margin-bottom: 20px;
}

.close {
  color: orangered;
  background-color: #AED4F89B;
  float: right;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 20px;
}

.title-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.title {
  font-family: 'Coda Caption', sans-serif;
  font-size: 50px;
  color: black;
}

.search-btn {
  min-width: 250px;
  background-color: #AED4F89B;
  color: rgb(29, 27, 27);
  border-radius: 25px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  padding-left:10px;
  padding-right: 10px;
}

#authModal {
  background: #fff;
  border-radius: 10px;
}
 
#date::-webkit-input-placeholder{
    color: black;
    padding: 5px;
}
 
#date::-moz-placeholder{
    color: black;
    padding: 5px;
}
 
#date::-ms-input-placeholder{
    color: black;
    padding: 5px;
}
 
#date::placeholder{
    color: black;
    padding: 5px;
}

#date {
    border-radius: 10px;
    background: none;
    padding: 0;
}

.searchIcon {
    cursor: pointer;
    color: blue;
    float: right;
    width: 50px;
    height: 40px;
    border-radius: 50%;
    background: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

@media (max-width:600px) {
  #authModal {
    width: 300px;
  }
  .title{
    font-size: 35px;
    padding-bottom: 15px;
  }
}


