.postForm {
  background-color: #fff;
  min-height: 500px;
  width: 100%;
  border-radius: 20px;
  border: 1px solid #1477D3;
  margin-top: 40px;
}

.post-label {
    color: white;
    background-color: #D29E7A;
    padding: 20px;
    cursor: pointer;
    text-align: center;
    margin-top: 10px;
    margin-bottom: -10px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

.postHeading {
    text-decoration: underline;
    text-transform: capitalize;
    margin-left: 20px;
    margin-bottom: 40px;
    font-weight: 700;
    font-family: 'Poppins';
    color: #03134e;
}

.postBtn {
    margin-left: 20px;
    background: #03134e;
    color: #fff;
    margin-bottom: 10px;
}

.input-field label {
    color: #03134e;
    font-size: medium;
    font-family: 'Poppins';
}

.noteDiv {
    margin-left: 10px;
    padding: 10px;
}

.noteDiv h6 {
    color: red;
    font-weight: 600;
    font-family: 'Poppins';
}

.noteDiv p {
    color: grey;
    font-weight: 500;
    font-family: 'Poppins';
}


